	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of Triangles</h2>\n<p>The area of a triangle is the total enclosed space on the two-dimensional plane by the sides of the triangle.</p>\n<p><img src=\"http://www.mathportal.org/calculators/plane-geometry-calculators/triangleEquilateral.gif\" alt=\"Triangle\"></p>\n<p>The basic formula to find the area of a triangle is,</p>\n<h4>Formula</h4>\n<p><strong>Area = 0.5 * base of the triangle * height of the triangle</strong></p>\n<p>For example in the image above,</p>\n<p>Area = 0.5 * a * h</p>\n<h3>More Information</h3>\n<p>For a full list of other advanced fourmulas, click <a href=\"http://www.onlinemathlearning.com/area-triangle.html\">here</a></p>","fields":{"slug":"/mathematics/area-of-triangles/"},"frontmatter":{"title":"Area of Triangles"}}},"pathContext":{"slug":"/mathematics/area-of-triangles/"}}

